/* WINDOW */

.widow-container {
  position: fixed;
  width: 100vw;
  height: 100vw;
}

.window {
  background: -webkit-gradient(linear, left top, right top, from(rgba(120, 120, 150, 1)), to(rgba(6, 38, 89, 1)));
  background: linear-gradient(to right, rgba(120, 120, 150, 1), rgba(6, 38, 89, 1));
  width: 80vw;
  min-height: 40vh;
  left: 50%;
  top: 200px;
  margin-left: -40vw;
  position: fixed;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 10px 4px rgba(255, 255, 255, 0.5);
          box-shadow: 0 0 10px 4px rgba(255, 255, 255, 0.5);
  overflow: auto;
}

.window-bar {
  position: relative;
  width: 100%;
  min-height: 40px;
  background: -webkit-gradient(linear, left top, right top, from(rgba(100, 100, 100, 0.8)), to(rgba(50, 50, 50, 0.8)));
  background: linear-gradient(to right, rgba(100, 100, 100, 0.8), rgba(50, 50, 50, 0.8));
  color: rgb(250, 250, 250);
  line-height: 40px;
}

.window-title {
  margin: auto;
  font-weight: bold;
}

.close {
  color: rgb(127, 31, 31);
  font-size: 36px;
  font-weight: bold;
  position: absolute;
  right: 6px;
  top: -2px;
  text-shadow: 0 0 10px white;
  cursor: pointer;
}

.close:hover {
  text-shadow: 0 0 10px red, 0 0 20px red;
}

